
/**
 * vuex管理登陆状态，具体可以参考官方登陆模板示例
 */
import {
	mapMutations,
	mapState
} from 'vuex';

export default {
	computed: {
		...mapState(['userInfo'])
	},
	methods: {
		...mapMutations(['login', 'logout'])
	},
	onLaunch: function() {
		let userInfo = uni.getStorageSync('userInfo') || '';
		if (userInfo.id) {

			//更新登陆状态
			uni.getStorage({
				key: 'userInfo',
				success: (res) => {
					this.login(res.data);
				}
			});
			uni.request({
				url: this.api + '/api/loadUserInfo',
				data: {
					token: this.userInfo.token
				},
				success: (res) => {
					// console.log(res.data.code)
					if (res.data.code == 0) {
						//更新登陆状态
						this.$api.msg(res.data.msg)
						// console.log('登录状态失败')
						this.logout();
						// uni.navigateTo({
						// 	url:'/pages/public/login'
						// })
					} else if (res.data.code == 1) {
						console.log('登录状态成功')
					} else if (res.data.code == -1) {
						console.log('登录状态异常')
						this.logout();
					} else if (res.data.code == -2) {
						console.log('登录状态超时')
						this.logout();
					}

				}
			});
		} else {
			this.$api.msg('还未登录')
			setTimeout(function() {
				uni.navigateTo({
					url:'/pages/public/login'
				})
			}, 1500)
			
		}

	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	},
}
